<template>
  <div class="nav-div">
    <div>
      <img src="../../assets/1.jpg" />
      <i class="iconfont iconshezhi" @click="show"></i>
    </div>
    <div>
      <el-dialog title="设置" :visible.sync="dialogConFormVisible">
        <el-form v-model="form">
          <el-form-item label="是否开启相机检测">
            <el-switch
              v-model="form.setcamera"
              active-color="#13ce66"
              inactive-color="#ff4949"
              style="float: left; margin: 10px"
            >
            </el-switch>
          </el-form-item>
          <el-form-item label="开启系统控制">
            <el-switch
              v-model="form.setpower"
              active-color="#13ce66"
              inactive-color="#ff4949"
              style="float: left; margin: 10px"
            >
            </el-switch>
          </el-form-item>
          <el-form-item label="是否检测托盘">
            <el-switch
              v-model="form.settray"
              active-color="#13ce66"
              inactive-color="#ff4949"
              style="float: left; margin: 10px"
            >
            </el-switch>
          </el-form-item>
        </el-form>
        <div slot="footer">
          <el-button @click="clear">取消</el-button>
          <el-button type="primary" @click="set_up">确认</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavMenu",
  data() {
    return {
      dialogConFormVisible: false,
      form: {
        setcamera: false,
        setpower: false,
        settray: false,
      },
      stes: [],
    };
  },
  methods: {
    clear() {
      this.dialogConFormVisible = false;
    },
    show() {
      this.loadSet();
      this.dialogConFormVisible = true;
    },
    loadSet() {
      this.$axios
        .get("/config/find?id=601764207ab6bd57abbe0af0", {})
        .then((resp) => {
          this.form.setcamera = resp.data.data.setcamera;
          this.form.setpower = resp.data.data.setpower;
          this.form.settray = resp.data.data.settray;
        });
    },
    set_up() {
      this.$confirm("确认更改设置？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$axios
          .post("/config/save", {
            id: "601764207ab6bd57abbe0af0",
            setcamera: this.form.setcamera,
            setpower: this.form.setpower,
            settray: this.form.settray,
          })
          .then((resp) => {
            this.$emit("set_up");
            this.dialogConFormVisible = false;
          });
      });
    },
  },
};
</script>

<style>
.nav-div {
  width: 100%;
  background-color: rgb(253, 209, 0);
  overflow: hidden;
}

.nav-div img {
  width: 110px;
  float: left;
  margin: auto 20px;
}

.el-icon-switch-button {
  cursor: pointer;
  outline: 0;
  float: right;
  font-size: 40px;
  color: #222;
  padding: 10px;
}

.iconshezhi {
  float: right;
  margin-top: 13px;
  margin-right: 13px;
  font-size: 28px;
}
</style>


